<template>
  <div class="container">
    <!-- 输入行 -->
    <el-row class="input-row" :gutter="20" type="flex" justify="center">
      <el-col :xs="24" :sm="18" :md="12" :lg="12" :xl="12">
        <el-input
          v-model="input"
          placeholder="请输入域名或者IP"
          @keydown.ctrl.enter="connectWebSocket"
        ></el-input>
      </el-col>
      <el-col :xs="24" :sm="6" :md="2" :lg="2" :xl="2" class="send-button-col">
        <el-button
          v-if="!buttonDisabled"
          @click="connectWebSocket"
          :disabled="!input"
        >
          Send
        </el-button>
        <el-button
          v-else
          @click="disconnectWebSocket"
          type="danger"
        >
          Stop
        </el-button>
      </el-col>
    </el-row>
    <!-- 大表格 -->
    <el-table v-loading="loading" :data="Object.values(groupedData)" height="500" border>
      <el-table-column type="expand">
        <template #default="props">
          <!-- 小表格 -->
          <el-table :data="props.row" height="300" border>
            <el-table-column prop="id" label="序号" sortable></el-table-column>
            <el-table-column prop="Host" label="主机" sortable></el-table-column>
            <el-table-column prop="Loss" label="丢包率" sortable></el-table-column>
            <el-table-column prop="Last" label="最后一次" sortable></el-table-column>
            <el-table-column prop="Avg" label="平均" sortable></el-table-column>
            <el-table-column prop="Best" label="最佳" sortable></el-table-column>
            <el-table-column prop="Wrst" label="最差" sortable></el-table-column>
            <el-table-column prop="StDev" label="标准差" sortable></el-table-column>
          </el-table>
        </template>
      </el-table-column>
      <el-table-column label="Provide">
        <template #default="{ row }">
          {{ row[0].clientName }}
          <el-link type="primary" :href="row[0].clientUrl" target="_blank" class="client-url-link">
            <el-icon><TopRight /></el-icon>
          </el-link>
        </template>
      </el-table-column>
      <el-table-column label="Send" prop="length" sortable>
    <template #default="{ row }">
      {{ row.length }}
    </template>
  </el-table-column>
      <el-table-column label="Loss" prop="averageLoss" sortable>
        <template #default="{ row }">
          {{ calculateAverageLoss(row) }}%
        </template>
      </el-table-column>
      <el-table-column label="Best" prop="minBest" sortable>
        <template #default="{ row }">
          {{ calculateMinBest(row) }}
        </template>
      </el-table-column>
      <el-table-column label="Wrst" prop="maxWrst" sortable>
        <template #default="{ row }">
          {{ calculateMaxWrst(row) }}
        </template>
      </el-table-column>
      <el-table-column label="Host" prop="frequentHost" sortable>
        <template #default="{ row }">
          {{ mostFrequentHost(row) }}
        </template>
      </el-table-column>
    </el-table>
  </div>
  <footer>
  Copyright &copy; CSZnet | Website: <a target="_blank" href="https://www.csz.net">www.csz.net</a>
</footer>
</template>
  
  <script>
import CryptoJS from 'crypto-js';

export default {
  data() {
    return {
      input: "",
      socket: null,
      tableData: [],
      buttonDisabled: false,
      activePanels: [],
      loading: false,
    };
  },
  computed: {
  groupedData() {
    return this.tableData.reduce((acc, cur) => {
      if (!acc[cur.uuid]) {
        acc[cur.uuid] = [];
      }
      acc[cur.uuid].push(cur);
      return acc;
    }, {});
  },
  formattedGroupedData() {
    let formattedData = [];
    for (const key in this.groupedData) {
      let group = this.groupedData[key];
      formattedData.push({
        data: group,
        length: group.length,
      });
    }
    return formattedData;
  },
},
  methods: {
    isDomainOrIP(input) {
    const domainRegex = /^(?=.{1,255})(?:(?!-)[A-Za-z0-9-]{1,63}(?<!-)\.?)+(?:[A-Za-z]{2,6})$/;
    const ipv4Regex = /^((25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;

    return domainRegex.test(input) || ipv4Regex.test(input);
  },
    onDataReceived(newData) {
      // 根据 clientName 找到对应的 groupedData 项
      let entry = this.groupedData.find((item) => item.clientName === newData.clientName);

      // 如果没有找到，创建一个新的项并添加到 groupedData 中
      if (!entry) {
        entry = {
          clientName: newData.clientName,
          clientUrl: newData.clientUrl,
          data: [],
        };
        this.groupedData.push(entry);
      }

      // 将新数据添加到对应的客户端数据数组中
      entry.data.push(newData);

      // 重新设置 groupedData，以触发视图更新
      this.groupedData = [...this.groupedData];
    },
    generateToken(time) {
      const hash = CryptoJS.SHA256(`${time}bbbb`);
      return hash.toString(CryptoJS.enc.Hex);
    },
    async createRequestMessage(type, content) {
      const time = Math.floor(Date.now() / 1000).toString();
      const token = this.generateToken(time);
      return {
        time: time,
        type: type,
        content: content,
        token: token,
      };
    },
    async connectWebSocket() {
      if (!this.isDomainOrIP(this.input)||!this.input) {
        this.$message.error("请输入域名或者IP");
        return;
      }

      if (!this.socket || this.socket.readyState !== WebSocket.OPEN) {
        // this.socket = new WebSocket(`wss://wss.csz.net/ws`);
        this.socket = new WebSocket(`ws://192.168.88.9:8080/ws`);
        this.socket.addEventListener("open", async () => {
          const req = await this.createRequestMessage("ping", this.input);
          this.socket.send(JSON.stringify(req));
          this.buttonDisabled = true;
        });
        this.tableData = [];
        this.loading = true;
        this.socket.addEventListener("message", (event) => {
        try {
          const data = JSON.parse(event.data);
          this.tableData.push(data);
          this.loading = false;

          // 检查每个 UUID 的数据数量
        const uuidCount = this.tableData.reduce((countMap, item) => {
          countMap[item.uuid] = (countMap[item.uuid] || 0) + 1;
          return countMap;
        }, {});
        
        // 检查所有 UUID 的数据数量是否都达到 10 条
        const allUuidsHaveTenItems = Object.values(uuidCount).every(
          (count) => count >= 10
        );
        if (allUuidsHaveTenItems) {
          // 将 Stop 变回 Send
            this.disconnectWebSocket();
        }

          
          } catch (error) {
            console.error("Received invalid JSON:", event.data);
          }
          this.loading = false;
        });

        // this.socket.addEventListener("close", () => {
        //   this.$message.warning("WebSocket连接已关闭");
        //   this.buttonDisabled = false;
        // });

        this.socket.addEventListener("error", () => {
          this.$message.error("服务端握手失败");
          // console.error("WebSocket error:", event);
          this.buttonDisabled = false;
        });
      } else {
        const req = await this.createRequestMessage("ping", this.input);
        this.socket.send(JSON.stringify(req));
        this.buttonDisabled = true;
      }
    },
    disconnectWebSocket() {
      if (this.socket) {
        this.socket.close();
        this.socket = null;
        this.buttonDisabled = false;
        this.loading = false; // 更新 loading 状态
      }
    },
    mostFrequentHost(group) {
      const hostCounts = group.reduce((acc, cur) => {
        if (!acc[cur.Host]) {
          acc[cur.Host] = 0;
        }
        acc[cur.Host]++;
        return acc;
      }, {});

      let maxCount = 0;
      let mostFrequentHost = '';

      for (const host in hostCounts) {
        if (hostCounts[host] > maxCount) {
          maxCount = hostCounts[host];
          mostFrequentHost = host;
        }
      }

      return mostFrequentHost;
    },
    togglePanel(uuid) {
      const index = this.activePanels.indexOf(uuid);
      if (index === -1) {
        this.activePanels.push(uuid);
      } else {
        this.activePanels.splice(index, 1);
      }
    },
    calculateAverageLoss(group) {
      const totalLoss = group.reduce((sum, item) => sum + item.Loss, 0);
      return (totalLoss / group.length).toFixed(2);
    },
    calculateMinBest(group) {
      return Math.min(...group.map((item) => item.Best));
    },
    calculateMaxWrst(group) {
      return Math.max(...group.map((item) => item.Wrst));
    },
  },
};
</script>
  
<style scoped>
.container {
  margin-top: 20px;
  padding-left: 10px;
  padding-right: 10px;
}

.input-row {
  margin-bottom: 20px;
}

.input-row {
  margin-bottom: 20px;
  margin-left: 0 !important;
  margin-right: 0 !important;
}



@media (max-width: 767px) {
  .container {
    padding-left: 5px;
    padding-right: 5px;
  }

  .send-button-col {
    margin-top: 20px;
  }
}
footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  padding: 10px;
  background-color: #f9f9f9; /* 可以设置为你喜欢的颜色 */
  border-top: 1px solid #eaeaea;
}

footer a {
  color: #333; /* 可以设置为你喜欢的颜色 */
  text-decoration: none;
}

footer a:hover {
  text-decoration: underline;
}
</style>