import { createApp } from 'vue'
import App from './App.vue'
import { createMetaManager } from 'vue-meta'; // 从 'vue-meta' 导入 createMetaManager
import router from './router'
import store from './store'
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
import * as ElementPlusIconsVue from '@element-plus/icons-vue' // 导入所有图标

const app = createApp(App)
const metaManager = createMetaManager(); // 创建 meta 管理器实例
app.use(metaManager); // 使用 meta 管理器
app.use(store).use(router).use(ElementPlus)

// 全局注册所有图标
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component)
}

app.mount('#app')

router.afterEach((to) => {
  if (to.meta && to.meta.title) {
    document.title = to.meta.title;
  } else {
    document.title = 'CSZnet'; // 如果没有设置标题，使用默认标题
  }
});